import React, {useEffect, useState, useRef} from "react";
import {useDispatch, useSelector} from "react-redux";
import {connect} from "./redux/blockchain/blockchainActions";
import {fetchData} from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";
import "./test.css"
import logo_discord from './asset/discord.png';
import logo_opensea from './asset/opensea.png';
import logo_ins from './asset/ins.png';
import logo_twitter from './asset/twitter.png';
import logo_mint from './asset/Mint.png';

import img2 from './asset/2.png';
import img3 from './asset/3.png';
import img4 from './asset/4.png';
import img5 from './asset/5.png';
import img6 from './asset/6.png';
import Modal from 'react-modal';

Modal.setAppElement('#root');

const truncate = (input, len) =>
  input.length > len ? `${input.substring(0, len)}...` : input;

export const StyledButton = styled.button`
  padding: 2%;
  border-radius: 50px;
  border: none;
  font-size: 1.5rem;

  background: rgba(41, 0, 8, 0.3);
  font-weight: bold;
  color: #E25856FF;
  width: 60%;
  cursor: pointer;

  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const StyledRoundButton = styled.button`
  border: none;
  border-radius:40% ;
  font-weight: bold;
  font-size: 2rem;
  color: #F5784CFF;
  width: 25px;
  height: 25px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(41, 0, 8, 0);


`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const StyledLogo = styled.img`
  width: 200px;
  @media (min-width: 767px) {
    width: 300px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;

export const StyledImg = styled.img`
  box-shadow: 0px 5px 11px 2px rgba(0, 0, 0, 0.7);
  border: 4px dashed var(--secondary);
  background-color: var(--accent);
  border-radius: 100%;
  width: 200px;
  @media (min-width: 900px) {
    width: 250px;
  }
  @media (min-width: 1000px) {
    width: 300px;
  }
  transition: width 0.5s;
`;

export const StyledLink = styled.a`
  color:  #F5784CFF;
  text-decoration: none;
`;




export const StyleCol = styled.div`
  grid-area: 1 / ${({index}) => (index ? index : 0)} / 2 / ${({index}) => (index ? index + 1 : 0)};
  text-align:center
  margin:10px;
  color: var(--secondary);
  text-decoration: none;
`;

export const StyleRow = styled.div`
  display: grid;
  grid-template-columns: 15fr 20fr 30fr 20fr 15fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
`;
const StyleBackGround = styled.div`
  
`



function MantonItem(props) {
  console.log(props.info);
  return (
    <div className="card" style={{zIndex: props.info.zIndex}} >
      <img className="img-card" src={props.info.cardImg} />
      <div className="content">
        <a className="logo-link" href={props.info.link}>
          <img className="img-logo" src={props.info.logo} />
        </a>
      </div>
    </div>
  );
}

function MantonItemMobile(props) {
  console.log(props.info);
  if (props.info.title != "MINT") {
    return (
      <a className="m-button" href={props.info.link}>
        <div style={{width: "100%", backgroundColor: "#100404FF", padding: "10px", color: "#FFFFFFFF", textAlign: "center", borderRadius: "10px", fontSize: "1.5rem"}}>
          {props.info.title}
        </div>
      </a>
    );
  } else {
    return (

      <div style={{width: "60%", textDecoration: "none"}}>
        <div style={{width: "100%", backgroundColor: "#100404FF", padding: "10px", color: "#FFFFFFFF", textAlign: "center", borderRadius: "10px", fontSize: "1.5rem"}}>
          {props.info.title}
        </div>
      </div>
    )
  }
}

function MantonItemIns(props) {
  console.log(props.info);
  return (
    <div className="card" style={{zIndex: props.info.zIndex}} >
      <img className="img-card" src={props.info.cardImg} />
      <div className="content"
      >
        <a className="logo-link-ins"
          marginLeft="20%" width="60%"
          href={props.info.link}>
          <img className="img-logo" src={props.info.logo} />
        </a>
      </div>
    </div>
  );
}
const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    backgroundColor: "#380E0FFF",
    position: "relative",
    borderRadius: "10px",
    width: "90%  "
  },
};
function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState(``);
  const [mintAmount, setMintAmount] = useState(1);
  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });
  const [hiddenTeam, setHidenTeam] = useState(true);
  const claimNFTs = () => {
    let cost = CONFIG.WEI_COST;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);
    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`Minting your ${CONFIG.NFT_NAME}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("something wrong, please try again");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `WOW, the ${CONFIG.NFT_NAME} is yours! go visit Opensea.io to view it.`
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > 5) {
      newMintAmount = 5;
    }
    setMintAmount(newMintAmount);
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();
  }, []);

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  const items = {
    dis: {
      title: "DISCORD",
      cardImg: img2,
      link: "https://discord.gg/qdj4rKaYJv",
      logo: logo_discord,
      zIndex: 1,

    },
    opensea: {
      title: "OPENSEA",
      cardImg: img3,
      link: "https://opensea.io/collection/mansion-club",
      logo: logo_opensea,
      zIndex: 2,
    },
    mint: {
      title: "MINT",

      cardImg: img4,
      link: "/Mint",
      logo: logo_opensea,
      zIndex: 3,

    },
    twitter: {
      title: "TWITTER",
      cardImg: img5,
      link: "https://twitter.com/mansionclub3",
      logo: logo_twitter,
      alignItems: "right",

      zIndex: 2,

    },
    ins: {
      title: "INS",
      cardImg: img6,
      link: "https://www.instagram.com/mansionclub.xyz/",
      logo: logo_ins,
      zIndex: 1,
    },

  }
  let subtitle;
  const [modalIsOpen, setIsOpen] = React.useState(false);

  function openModal(e) {
    e.preventDefault();
    console.log("open modal");
    setIsOpen(true);
  }

  function afterOpenModal() {
    // references are now sync'd and can be accessed.
  }

  function closeModal() {
    setIsOpen(false);
  }

  function Mint() {

    return (
      <div style={{width: "95%", borderRadius: "10px", margin: "auto", maxWidth: "600px", display: "flex", flexDirection: "column", alignItems: "center"}} >


        <s.StyleText
          style={{
            textAlign: "center",
            fontWeight: "bold",
          }}
        >
          {data.totalSupply} / {CONFIG.MAX_SUPPLY}
        </s.StyleText>

        {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
          // Mint out of total supply
          <>
            <s.TextTitle
              style={{textAlign: "center", color: "var(--accent-text)"}}
            >
              The sale has ended.
            </s.TextTitle>
            <s.TextDescription
              style={{textAlign: "center", color: "var(--accent-text)"}}
            >
              You can still find {CONFIG.NFT_NAME} on
            </s.TextDescription>
            <s.SpacerSmall />
            <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
              {CONFIG.MARKETPLACE}
            </StyledLink>
          </>
        ) : (
          <>

            {blockchain.account === "" ||
              blockchain.smartContract === null ? (
              <s.Container ai={"center"} jc={"center"}>
                <StyledButton
                  onClick={(e) => {
                    e.preventDefault();
                    dispatch(connect());
                    getData();
                  }}
                >
                  Connect MetaMask
                </StyledButton>
                {blockchain.errorMsg !== "" ? (
                  <>
                    <s.SpacerSmall />
                    <s.StyleText
                      style={{
                        textAlign: "center",

                      }}
                    >
                      {blockchain.errorMsg}
                    </s.StyleText>
                  </>
                ) : null}
              </s.Container>
            ) : (
              <>

                <s.Container ai={"center"} jc={"center"} fd={"row"}>
                  <StyledRoundButton
                    style={{lineHeight: 0.4}}
                    disabled={claimingNft ? 1 : 0}
                    onClick={(e) => {
                      e.preventDefault();
                      decrementMintAmount();
                    }}
                  >
                    -
                  </StyledRoundButton>
                  <div
                    style={{
                      textAlign: "center",
                      color: "#F5784CFF",
                      width: "30%",
                      margin: "0px", fontSize: "1.5rem", background: " rgba(41, 0, 8, 0.4)",


                    }}
                  >
                    {mintAmount}
                  </div>
                  <StyledRoundButton
                    disabled={claimingNft ? 1 : 0}
                    onClick={(e) => {
                      e.preventDefault();
                      incrementMintAmount();
                    }}
                  >
                    +
                  </StyledRoundButton>
                </s.Container>
                <s.Container ai={"center"} jc={"center"} fd={"row"}>

                  {claimingNft ? (<StyledButton
                    disabled={claimingNft ? 1 : 0}
                    onClick={(e) => {
                      e.preventDefault();
                      claimNFTs();
                      getData();
                    }}
                  >
                    {"Waiting"}
                  </StyledButton>)
                    :
                    (
                      <img width={"40%"}
                        disabled={claimingNft ? 1 : 0}
                        cursor={"pointer"}
                        style={{cursor: "pointer"}}
                        onClick={(e) => {
                          e.preventDefault();
                          claimNFTs();
                          getData();
                        }}
                        src={logo_mint} />
                    )}

                </s.Container>


                <s.StyleText
                  style={{
                    textAlign: "center",
                  }}
                >
                  {feedback}
                </s.StyleText>
                <s.StyleText
                  style={{textAlign: "center", }}
                >
                  1 NFT = {CONFIG.DISPLAY_COST}                        {CONFIG.NETWORK.SYMBOL}
                </s.StyleText>

              </>
            )}
          </>
        )}
        <s.StyleText
          style={{
            textAlign: "center",
          }}
        >
          Contract: <StyledLink target={"_blank"} href={CONFIG.SCAN_LINK}>
            {truncate(CONFIG.CONTRACT_ADDRESS, 12)}
          </StyledLink>
        </s.StyleText>
      </div>

    )
  }

  if (
    navigator.userAgent.match(/Mobi/i) ||
    navigator.userAgent.match(/Android/i) ||
    navigator.userAgent.match(/iPhone/i)
  ) {
    // 当前设备是移动设备
    return (


      <>
        <div className="bg-mobile">

          <div style={{
            borderRadius: "20px", height: "40%", maxHeight: "600px", display: "flex", flexDirection: "column", justifyContent: "space-around", alignItems: "center",
            position: "absolute", bottom: "20%",width:"100%"
          }}>
            <div className="m-button">
              <div style={{width: "100%", backgroundColor: "#100404FF", padding: "10px", color: "#FFFFFFFF", textAlign: "center", borderRadius: "10px", fontSize: "1.5rem"}}
                onClick={openModal}
              >
                {items.mint.title}
              </div>
            </div>
            <MantonItemMobile info={items.opensea} />
            <MantonItemMobile info={items.twitter} />
            <MantonItemMobile info={items.dis} />

            <MantonItemMobile info={items.ins} />
          </div>
          <Modal
            isOpen={modalIsOpen}
            onAfterOpen={afterOpenModal}
            onRequestClose={closeModal}
            style={customStyles}
            contentLabel="Example Modal"
          >
            <Mint />
          </Modal>
        </div>
      </>
    )
  } else {
    return (

      <>
        <div className="background">

          <img className="about" onClick={
            () => {
              const a = hiddenTeam;
              setHidenTeam(!a);
            }
          } />
          <img className="flyBird" />
          <div className="btn-group">
            <MantonItem info={items.dis} />
            <MantonItem info={items.opensea} />
            <div className="card" style={{zIndex: items.mint.zIndex}} >
              <img className="img-card" src={items.mint.cardImg} />
              <div className="content" style={{marginLeft: "10%"}} >


                <s.StyleText
                  style={{
                    marginTop: "40%",
                    textAlign: "center",
                    fontWeight: "bold",
                  }}
                >
                  {data.totalSupply} / {CONFIG.MAX_SUPPLY}
                </s.StyleText>

                {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
                  // Mint out of total supply
                  <>
                    <s.TextTitle
                      style={{textAlign: "center", color: "var(--accent-text)"}}
                    >
                      The sale has ended.
                    </s.TextTitle>
                    <s.TextDescription
                      style={{textAlign: "center", color: "var(--accent-text)"}}
                    >
                      You can still find {CONFIG.NFT_NAME} on
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                      {CONFIG.MARKETPLACE}
                    </StyledLink>
                  </>
                ) : (
                  <>

                    {blockchain.account === "" ||
                      blockchain.smartContract === null ? (
                      <s.Container ai={"center"} jc={"center"}>
                        <StyledButton
                          onClick={(e) => {
                            e.preventDefault();
                            dispatch(connect());
                            getData();
                          }}
                        >
                          Connect MetaMask
                        </StyledButton>
                        {blockchain.errorMsg !== "" ? (
                          <>
                            <s.SpacerSmall />
                            <s.StyleText
                              style={{
                                textAlign: "center",

                              }}
                            >
                              {blockchain.errorMsg}
                            </s.StyleText>
                          </>
                        ) : null}
                      </s.Container>
                    ) : (
                      <>

                        <s.Container ai={"center"} jc={"center"} fd={"row"}>
                          <StyledRoundButton
                            style={{lineHeight: 0.4}}
                            disabled={claimingNft ? 1 : 0}
                            onClick={(e) => {
                              e.preventDefault();
                              decrementMintAmount();
                            }}
                          >
                            -
                          </StyledRoundButton>
                          <div
                            style={{
                              textAlign: "center",
                              color: "#F5784CFF",
                              width: "30%",
                              margin: "0px", fontSize: "1.5rem", background: " rgba(41, 0, 8, 0.4)",


                            }}
                          >
                            {mintAmount}
                          </div>
                          <StyledRoundButton
                            disabled={claimingNft ? 1 : 0}
                            onClick={(e) => {
                              e.preventDefault();
                              incrementMintAmount();
                            }}
                          >
                            +
                          </StyledRoundButton>
                        </s.Container>
                        <s.Container ai={"center"} jc={"center"} fd={"row"}>

                          {claimingNft ? (<StyledButton
                            disabled={claimingNft ? 1 : 0}
                            onClick={(e) => {
                              e.preventDefault();
                              claimNFTs();
                              getData();
                            }}
                          >
                            {"Waiting"}
                          </StyledButton>)
                            :
                            (
                              <img width={"40%"}
                                disabled={claimingNft ? 1 : 0}
                                cursor={"pointer"}
                                style={{cursor: "pointer"}}
                                onClick={(e) => {
                                  e.preventDefault();
                                  claimNFTs();
                                  getData();
                                }}
                                src={logo_mint} />
                            )}

                        </s.Container>


                        <s.StyleText
                          style={{
                            textAlign: "center",
                          }}
                        >
                          {feedback}
                        </s.StyleText>
                        <s.StyleText
                          style={{textAlign: "center", }}
                        >
                          1 NFT = {CONFIG.DISPLAY_COST}                        {CONFIG.NETWORK.SYMBOL}
                        </s.StyleText>

                      </>
                    )}
                  </>
                )}
                <s.StyleText
                  style={{
                    textAlign: "center",
                  }}
                >
                  Contract: <StyledLink target={"_blank"} href={CONFIG.SCAN_LINK}>
                    {truncate(CONFIG.CONTRACT_ADDRESS, 12)}
                  </StyledLink>
                </s.StyleText>
              </div>
            </div>
            <MantonItem info={items.twitter} />
            <MantonItemIns info={items.ins} />


          </div>

          <div className="foreGround" />
          <img className="teamMember" hidden={hiddenTeam} onClick={
            () => {
              setHidenTeam(true)
            }
          } />
        </div>
      </>
    );
  }

}

export default App;
